import React from 'react';
import jeetarfooter from '../img/jeetarfooter.svg';
import facebook from '../img/facebook.svg';
import instagram from '../img/instagram.svg';
import twitter from '../img/twitter.svg';
import youtube from '../img/youtube.svg';

import googleplay from '../img/googleplay.svg';
import appstore from '../img/appstore.svg';
import { Link} from 'react-router-dom';
import styled from 'styled-components';



const Footer =()=>{
     return(
     		 <>
     		    <footer className="footer-section py-5 text-white">
					 <div className="container-fluid pt-5">
						<div className="row">
							<div className="col-sm-12 col-md-4 col-lg-4">
							   <ul className="footer-link-wrapper">
									<li className="mb-5">
										<Link to='/'><img src={jeetarfooter} alt="logo"  /> </Link>
									</li>
									<li className="store"> 
									   <Link to='/' className ="d-block d-sm-inline-block mb-3 me-sm-3"><img src={googleplay} alt="logo"/> </Link>
									   <Link to='/' className="d-block d-sm-inline-block mb-3"><img src={appstore} alt="logo" /> </Link>
									</li>
								</ul>
							</div>
							<div className="col-sm-12 col-md-4 col-lg-4">
							<ul className="footer-link-wrapper">
									<li><Link to='/Faq'>Faq</Link></li>
									<li><Link to='/'>&nbsp;</Link></li>
									<li><Link to='/Contact'>Contact Us</Link></li>
									<li><Link to='/Help'>Help</Link></li>
									<li><Link to='/Career'>Career</Link></li>

								</ul>

							</div>
							<div className="col-sm-12 col-md-4 col-lg-4">
								<ul className="footer-link-wrapper">
									<li><Link to='/Signup'>Sign Up</Link></li>
									<li><Link to='/OurBlog'>Our Blogs</Link></li>
									<li><Link to='/Covid-19'>Covid- 19</Link></li>
									<li><Link to='/Blogs'>Blogs</Link></li>
									<li><Link to='/All PRoduct'>All Products</Link></li>

								</ul>

							</div>
						</div>
						<hr className="bg-white" />
						<div className="row">
							<div className="col-lg-7 col-md-7 col-sm-12 order-last order-sm-first">
									<Social className=" social list-group list-group-horizontal border-0">
										<li><Link to='/' className="me-2"><img src={twitter} alt="logo"/> </Link></li>
										<li><Link to='/' className="me-2"><img src={instagram} alt="logo"/> </Link></li>
										<li><Link to='/' className="me-2"><img src={twitter} alt="logo"/> </Link></li>
										<li><Link to='/' className="me-2"><img src={youtube} alt="logo"/> </Link></li>
									</Social>							    

							</div>

							<div className="col-lg-3 col-md-3 col-sm-12">
							   <ul className="footer-link-wrapper">
									<li><Link to='/terms' className="me-5">Terms</Link> <Link to='/policy'>Privacy Policy</Link></li>		
								</ul>
							</div>


							<div className="col-lg-2 col-md-2 col-sm-12">
							   <ul className="footer-link-wrapper">
									<li><a>(c) 2021 Jeetar</a></li>	
								</ul>

							</div>
						</div>
					 </div>
                </footer>
     		 </>
     	)
}

const Social =styled.ul`
   list-style: none;
   @media(max-width: 1024px){
	   margin-left:15px;
   }
`;
export default Footer