import React, {Component} from "react";
import { Button,Modal } from 'react-bootstrap';
import styled from "styled-components";
import textlogo from "./../logo/textlogo.svg";
import continueRule from "./../logo/continueRule.svg";
import facebook from "./../logo/facebook.svg";
import google from "./../logo/google.svg";
import cancel from "./../logo/cancel.svg";
import './OTP.css'

const Register = styled.p`
	font-family: Gilmer Bold;
	font-style: normal;
	font-weight: bold;
	font-size: 18px;
	line-height: 22px;
	text-align: center;
	margin-buttom: 10px;
	color: #374b58;
`;

class OTP extends Component {
	constructor(props) {
		super(props);
				this.state = {
					show:false,
					otp: "",
                    sentOTP:"",
					errors: {
						otp: false,
					},
				};
			}
			
            handleOTP= event => {
                this.setState({ otp: event.target.value }, () => {
                  this.validateOTP();
                });
              };
        

			validateOTP = () => {
				const { otp, sentOTP } = this.state;
				this.setState({
					errors:{
						otp: (otp === sentOTP) ? null : 'Display error',
					}
			  })
			}

			handleClose = () => this.setState({show : false});
			
			handleShow = () => this.setState({show : true});

			handleSubmit = (event) => {
				event.preventDefault();
				const { otp} = this.state;
				this.setState({
					errors: {
						otp: otp === " ",
					},
				});
				const data = {
					otp

				};
                console.log(data);
				this.handleShow(); 
				
				
				//send  to dadabase;
				if(data.otp ){
					return (
						<>
						  <Modal show={this.state.show} onHide={this.handleClose}>
							<Modal.Header closeButton>
							  <Modal.Title>Modal heading</Modal.Title>
							</Modal.Header>
							<Modal.Body>Woohoo, you're reading this text in a modal!</Modal.Body>
							<Modal.Footer>
							  <Button variant="secondary" onClick={this.handleClose}>
								Close
							  </Button>
							  <Button variant="primary" onClick={this.handleClose}>
								Save Changes
							  </Button>
							</Modal.Footer>
						  </Modal>
						</>
					  );
				}
			};
    render(){
	return (
		<>
			<div
				className="modal fade"
				id="OTPModal"
				data-bs-backdrop="static"
				data-bs-keyboard="false"
				tabIndex="-1"
				aria-labelledby="staticBackdropLabel"
				aria-hidden="true"
			>
				<div className="modal-dialog">
					<div className="modal-content container">
						<div className="mt-5 border-0">
							<img
								src={cancel}
								type="button"
								className="close"
								alt="cancel"
								data-bs-dismiss="modal"
								aria-label="Close"
							/>
						</div>
						<div className="modal-body mt-n5">
							<Register>One Time Password</Register>

							<form
								onSubmit={this.handleSubmit}
								className="needs-validation"
							>
								<div className="container">
									<div className="row">
										<div className="col-12 position-relative">
                                                <label for="">Please enter the code sent to you at 0812345678</label>
											     <input
													type="text"
													className={
														"form-control form-field " +
														(this.state.errors.phone ? "error" : "")
													}
													name="otp"
                                                    maxLength='6'
                                                    id="otp"
													value={this.state.otp}
													onChange={this.handleOTP}
													onBlur={this.validateOTP}
												/>
                                                <div className="underline__wrapper d-flex position-absolute">
                                                    <div className="underline">
                                                    </div>
                                                    <div className="underline">
                                                    </div>
                                                    <div className="underline">
                                                    </div>
                                                    <div className="underline">
                                                    </div>
                                                    <div className="underline">
                                                    </div>
                                                    <div className="underline">
                                                    </div>
                                                </div>
                                            
						
												{this.state.errors.phone && (
													<div className="errorMessage">
														{this.state.errors.otp}
													</div>
												)}
										</div>
										<div className="col-12 form-group">
											<button
												type="submit"
												className="continue-with-phone form-control btn-primary"
											>
												Approve
											</button>
										</div>

                                        <div className="col-12 form-group">
											<p className="text-center">
                                                Havenâ€™t received your code? <span> Resend</span>
                                            </p>
										</div>
										
										</div>
								</div>
							</form>
						</div>
					</div>
				</div>
			</div>
		</>
	);
  }
};

export default OTP;
