
import React from 'react';
import styled from 'styled-components';


const Default =()=>{
     return(
     		 <>
     		    <DefaultSection className="default"> 
				   <h1>Page Not found</h1>
    
                </DefaultSection>
     		 </>
     	)
}

const DefaultSection =styled.div`
    height: 60vh;
    display: flex;
    justify-content:center;
    align-items:center;
    font-family: Gilmer Bold;
	font-style: normal;
	font-weight: bold;
	font-size: 18px;
	line-height: 22px;
	color: #374B58;	
`

export default Default;