import React from 'react';
import './MobileSlider.css'
import carousel__image from "./../img/carousel__image.svg";
import { Carousel } from 'react-bootstrap';
const MobileSlider = () =>{
    return(
        <div className="d-block d-sm-none carousel__wrapper">
            <Carousel fade  className="carousel">
                <Carousel.Item >
                    <img
                    className="d-block w-100 p-2    "
                    src={carousel__image}
                    alt="First slide"
                    />
                </Carousel.Item>
                <Carousel.Item>
                    <img
                    className="d-block w-100 p-2"
                    src={carousel__image}
                    alt="Second slide"
                    />
                </Carousel.Item>
                <Carousel.Item>
                    <img
                    className="d-block w-100 p-2"
                    src={carousel__image}
                    alt="Third slide"
                    />
                </Carousel.Item>
            </Carousel>
        </div>
    )

}


export default MobileSlider;