import React, {useState, Component} from "react";
import { Redirect } from "react-router";
import { Button,Modal } from 'react-bootstrap';
import bag from "./../img/bag.svg";
import man from "./../img/man.svg";
import herojeetarlogo from "./../img/herojeetarlogo.svg";
import continueRule from "./../logo/continueRule.svg";
import goods from "./../img/goods right.svg";
import facebook from "./../logo/facebook.svg";
import google from "./../logo/google.svg";
import OTP from "./OTP";
import "../component/Hero.css";
import "../component/Form.css";
import styled from "styled-components";



const Herologo = styled.img`
	margin-left: 100px;
	margin-top: 76px;
	@media (max-width: 1024px) {
		margin-left: 5px;
	}
`;

class Hero extends Component {
		constructor(props) {
			super(props);
					this.state = {
						show:false,
						countryCode: "+234",
						phone: "",
						errors: {
							countryCode: false,
							phone: '',
						},
					};
				}
				

				handleClose = () => this.setState({show : false});
				
				handleShow = () => this.setState({show : true});

				handleChange = (event) => {
					let nam = event.target.name;
					let val = event.target.value;
					this.setState({ [nam]: val }, () => {
						this.validatePhone();
					  });
				};

				validatePhone = () => {
					const { phone } = this.state;
					this.setState({
						errors:{
							phone: (!isNaN(phone) && phone.length === 11) ? null : 'Input a valid phone number',
						}
				  })
				}

				

				handleSubmit = (event) => {
					event.preventDefault();
					const { countryCode, phone} = this.state;
					this.setState({
						errors: {
							countryCode: countryCode === " ",
							phone: (phone === "" ||  phone.length !==11 || isNaN(phone)) ? 'phone number is required' : null,
		
						},
					});
					const data = {
						countryCode,
						phone,
	
					};
					if(this.state.errors.phone!==null){
						console.log('there are errors');
					}else{
						
						return <Redirect to="/jeetarbusiness" />
	
					}
                
				};

	render(){
	return (
		<>
			<div className="hero-section">
				<div className="row">
					<div className="col-sm-12 col-md-7 col-lg-7 d-none d-sm-block">
						<div className="container hero-left">
							<Herologo src={herojeetarlogo} alt="logo" className="mb-4 herologo" />
							<h1 className="fast-shoping">
								Fast Shopping, <br></br>Fast Shipping in Best Time
							</h1>
							<div className="image-wrapper tw-flex">
								<img
									src={bag}
									alt="ilustration1"
									className="ilustration1 img-fluid"
								/>
								<img
									src={man}
									alt="ilustration2"
									className="ilustration2 img-fluid"
								/>
							</div>
						</div>
					</div>
					<div className="col-sm-12 col-md-5 col-lg-5 hero-right">
						<div className="form-panel">
							<form
								onSubmit={this.handleSubmit}
								className="needs-validation"
							>
								<div className="container">
									<div className="form-group">
										<h5 className="text-center py-2 login__or__register">
											Login or Register
										</h5>
									</div>
									<div className="row g-2">
										<div className="col-3">
											<select value ={this.state.countryCode} onChange={this.handleChange} name="countryCode" className="form-select form-field">
											<option value="+234">+234</option>
											</select>
										</div>
										<div className="col-9">
											<div className="form-floating">
												<input
													type="text"
													className={
														"form-control form-field " +
														(this.state.errors.phone ? "error" : "")
													}
													placeholder="Phone Number"
													name="phone"
													value={this.state.phone}
													onChange={this.handleChange}
													onBlur={this.validatePhone}
												/>
												<label for="">Phone Number</label>
		
												{this.state.errors.phone && (
													<div className="errorMessage">
														{this.state.errors.phone}
													</div>
												)}
											</div>
										</div>
										<div className="col-12 form-group">
											<button
												type="submit"
												className="continue-with-phone form-control btn-primary"
											>
												Continue With Phone Number
											</button>
										</div>
										<div className="col-12 form-group mt-n5">
											<img
												src={continueRule}
												alt="continue"
												className="d-none d-sm-block mx-auto"
											/>
										</div>
										<div className="col-12 d-flex justify-content-center form-group text-center mt-0">
											<a href="#">
												<img src={google} className="mx-3" alt="google" />
											</a>
											<a href="#">
												<img
													src={facebook}
													className=" mx-3"
													alt="facebook"
												/>
											</a>
										</div>
									</div>
								</div>
							</form>
						</div>

						<img src={goods} alt="goods" className="goods" />
					</div>
				</div>
			</div>
		</>
	);
  }
}
;

export default Hero;
