import React, {Component} from "react";
import { Button,Modal } from 'react-bootstrap';
import styled from "styled-components";
import textlogo from "./../logo/textlogo.svg";
import continueRule from "./../logo/continueRule.svg";
import facebook from "./../logo/facebook.svg";
import google from "./../logo/google.svg";
import cancel from "./../logo/cancel.svg";

const Register = styled.p`
	font-family: Gilmer Bold;
	font-style: normal;
	font-weight: bold;
	font-size: 18px;
	line-height: 22px;
	text-align: center;
	margin-buttom: 10px;
	color: #374b58;
`;

class Login extends Component {
	constructor(props) {
		super(props);
				this.state = {
					show:false,
					countryCode: "+234",
					phone: "",
					errors: {
						countryCode: false,
						phone: '',
					},
				};
			}
			

			handleChange = (event) => {
				let nam = event.target.name;
				let val = event.target.value;
				this.setState({ [nam]: val }, () => {
					this.validatePhone();
				  });
			};

			validatePhone = () => {
				const { phone } = this.state;
				this.setState({
					errors:{
						phone: (!isNaN(phone) && phone.length === 11) ? null : 'Input a valid phone number',
					}
			  })
			}

			handleClose = () => this.setState({show : false});
			
			handleShow = () => this.setState({show : true});

			handleSubmit = (event) => {
				event.preventDefault();
				const { countryCode, phone} = this.state;
				this.setState({
					errors: {
						countryCode: countryCode === " ",
						phone: (phone === "" ||  phone.length !==11 || isNaN(phone)) ? 'phone number is required' : null,
	
					},
				});
				const data = {
					countryCode,
					phone,

				};
                console.log(data);
				this.handleShow(); 
				
				
				//send  to dadabase;
				if(data.phone){
					return (
						<>
						  <Modal show={this.state.show} onHide={this.handleClose}>
							<Modal.Header closeButton>
							  <Modal.Title>Modal heading</Modal.Title>
							</Modal.Header>
							<Modal.Body>Woohoo, you're reading this text in a modal!</Modal.Body>
							<Modal.Footer>
							  <Button variant="secondary" onClick={this.handleClose}>
								Close
							  </Button>
							  <Button variant="primary" onClick={this.handleClose}>
								Save Changes
							  </Button>
							</Modal.Footer>
						  </Modal>
						</>
					  );
				}
			};
    render(){
	return (
		<>
			<div
				className="modal fade"
				id="loginModal"
				data-bs-backdrop="static"
				data-bs-keyboard="false"
				tabIndex="-1"
				aria-labelledby="staticBackdropLabel"
				aria-hidden="true"
			>
				<div className="modal-dialog">
					<div className="modal-content container">
						<div className="mt-5 border-0">
							<img
								src={cancel}
								type="button"
								className="close"
								alt="cancel"
								data-bs-dismiss="modal"
								aria-label="Close"
							/>
						</div>
						<div className="modal-body mt-n5">
							<Register>Login or Register</Register>

							<form
								onSubmit={this.handleSubmit}
								className="needs-validation"
							>
								<div className="container">
									<div className="row g-2">
										<div className="col-3">
											<select value ={this.state.countryCode} onChange={this.handleChange} name="countryCode" className="form-select form-field">
											<option value="+234">+234</option>
											</select>
										</div>
										<div className="col-9">
											<div className="form-floating">
												<input
													type="text"
													className={
														"form-control form-field " +
														(this.state.errors.phone ? "error" : "")
													}
													placeholder="Phone Number"
													name="phone"
													value={this.state.phone}
													onChange={this.handleChange}
													onBlur={this.validatePhone}
												/>
												<label for="">Phone Number</label>
		
												{this.state.errors.phone && (
													<div className="errorMessage">
														{this.state.errors.phone}
													</div>
												)}
											</div>
										</div>
										<div className="col-12 form-group">
											<button
												type="submit"
												className="continue-with-phone form-control btn-primary"
											>
												Continue With Phone Number
											</button>
										</div>
										<div className="col-12 form-group mt-n5">
											<img
												src={continueRule}
												alt="continue"
												className="d-none d-sm-block mx-auto"
											/>
										</div>
										<div className="col-12 tw-flex tw-justify-center form-group text-center">
											<a href="#">
												<img src={google} className="mx-3" alt="google" />
											</a>
											<a href="#">
												<img
													src={facebook}
													className=" mx-3"
													alt="facebook"
												/>
											</a>
										</div>
									</div>
								</div>
							</form>
						</div>
					</div>
				</div>
			</div>
		</>
	);
  }
};

export default Login;
