import React from 'react';
import './Categorylist.css';
import img1 from './../img/new product.svg';
import img2 from './../img/special offer 1.svg';
import beverages from './../img/beverages.svg';
import fruits from './../img/fruits.svg';
import bakedgoods from './../img/baked goods.svg';
import food from './../img/food.svg';
import snacks from './../img/snacks.svg';
import icecream from './../img/ice cream.svg';
import readytoeat from './../img/ready to eat.svg';
import dairy from './../img/dairy and dell.svg';
import fit from './../img/fit and form.svg';
import personal from './../img/personal care.svg';
import Category from './../component/Category'
const Categorylist=()=>{

	const Categories =[
		{
		   id:1, 	
		   name:'New Product',
		   slug:'new_product',
		   img:img1
		},
		{
			id:2, 	
			name:'Special offers',
			slug:'special_offers',
			img:img2
		 },

		 {
			id:3, 	
			name:' Beverages',
			slug:'beverages',
			img:beverages
		 },
		 
		 {
			id:4, 	
			name:' Fruit & veg',
			slug:'fruit_and_vegetable',
			img:fruits
		 },

		 {
			id:5, 	
			name:'Baked goods',
			slug:'baked_goods',
			img:bakedgoods
		 },
		 {
			id:6, 	
			name:'Food',
			slug:'food',
			img:food
		 },

		 {
			id:7, 	
			name:'Snacks',
			slug:'snacks',
			img:snacks
		 },
		 
		 {
			id:8, 	
			name:'Ice cream',
			slug:'ice_cream',
			img:icecream
		 },

		 {
			id:9, 	
			name:'Ready to eat',
			slug:'ready_to_eat',
			img: readytoeat
		 },

		 {
			id:10, 	
			name:'Dairy and dell',
			slug:'dairy_and_dell',
			img: dairy
		 },

		 {
			id:11, 	
			name:'Fit and forms',
			slug:'fit_and_forms',
			img: fit
		 },
		
		 {
			id:12, 	
			name:'Personal Care',
			slug:'personal_care',
			img: personal
		 },

	]
     return(
     		 <>
			  <div className="category-section">
				<div className="container-fluid">
					<div className="section-heading">
						<h6 className="favourite-head">Browse Categories</h6>
					</div>
					<div className="categories">
						{
							Categories.map((category)=>{
								return(
								   <Category img={category.img} title={category.name} slug={category.slug} key={category.id} />
								);
							})
						}			
						
					</div>
	
				</div>
			  </div>
     	       
     		 </>
     	)
}

export default Categorylist