import React, { Component } from "react";
import isEmail from 'validator/lib/isEmail';
import textlogo from "./../logo/textlogo.svg";
import continueRule from "./../logo/continueRule.svg";
import facebook from "./../logo/facebook.svg";
import google from "./../logo/google.svg";
import cancel from "./../logo/cancel.svg";
import styled from "styled-components";

class Signup extends Component {
	constructor(props) {
		super(props);
		this.state = {
			countryCode: "+234",
			phone: "",
			fullname: "",
			email: "",
			password: "",
			repassword: "",
			errors: {
				countryCode:"",
				phone: "",
				fullname: "",
				email: "",
				password: "",
				repassword: "",
			},
		};
	}

	handlePhone = event => {
		this.setState({ phone: event.target.value }, () => {
		  this.validatePhone();
		});
	  };

	handleFullname= event => {
		this.setState({ fullname: event.target.value }, () => {
		  this.validateFullname();
		});
	  };

	handleEmail= event => {
		this.setState({ email: event.target.value }, () => {
		  this.validateEmail();
		});
	  };

	  handlePassword= event => {
		this.setState({ password: event.target.value }, () => {
		  this.validatePassword();
		});
	  };

	  handleRepassword= event => {
		this.setState({ repassword: event.target.value }, () => {
		  this.validateRepassword();
		});
	  };
      
      
	handleCheck= event => {
		this.setState({ check: event.target.value });
	  };


	validatePhone = () => {
		const { phone } = this.state;
		this.setState({
			errors:{
				phone: (!isNaN(phone) && phone.length === 11) ? null : 'Input a valid phone number',
			}
	  })
	}

	validateFullname = () => {
		const { fullname } = this.state;
		this.setState({
			errors:{
				fullname: (fullname.length) ? null : 'Input valid name',
			}
	  })
	}

	validateEmail = () => {
		const { email } = this.state;
		this.setState({
			errors:{
				email: (email.includes("@")) ? null : 'Input valid email',
			}
	  })
	}

	validatePassword = () => {
		const { password } = this.state;
		this.setState({
			errors:{
				password: (password.length <= 7) ? null : 'Invalid password',
			}
	  })
	}

	validateRepassword = () => {
		const { repassword} = this.state;
		this.setState({
			errors:{
				repassword: (repassword === this.state.password) ? null : 'Password does not match',
			}
	  })
	}

	handleSubmit = (event) => {
		event.preventDefault();
		const { countryCode, phone, fullname, email, password, repassword, check } = this.state;
		this.setState({
			errors: {
				countryCode: countryCode === " ",
				phone: (phone === "" ||  phone.length !==11 || isNaN(phone)) ? 'phone number is required' : null,
				fullname: (fullname === "") ? 'fullname is required' : null,
				email: (email === "") ? 'email is required' : null,
				password: (password === "") ? 'password is required' : null,
				repassword: (repassword === "") ? 'retype password is required' : null,
				check: (check === "") ? 'null' : null,
				

			},
		});
		const data = {
			countryCode,
			phone,
			fullname,
			email,
			password,
			check,
		};

		console.log("Data:", data);
		//send  to dadabase;
	};

	render() {
		return (
			<>
				<div
					className="modal fade"
					id="signupModal"
					data-bs-backdrop="static"
					data-bs-keyboard="false"
					tabIndex="-1"
					aria-labelledby="staticBackdropLabel"
					aria-hidden="true"
				>
					<div className="modal-dialog">
						<div className="modal-content container">
							<div className="mt-5 border-0">
								<div className="logo-wrapper">
									<div className="textlogo px-3 me-5">
										<img src={textlogo} alt="jeetar logo" />
									</div>
									<div className="already-sign-in-wrapper ms-5">
										<p className="already-sign-in pt-2">
											Already have an Account?{" "}
											<a href="#" className="text-primary signin">
												{" "}
												Sign In
											</a>
										</p>
									</div>
								</div>
								<img
									src={cancel}
									type="button"
									className="close"
									alt="cancel"
									data-bs-dismiss="modal"
									aria-label="Close"
								/>
							</div>
							<div className="modal-body mt-n5">
								<p className="sign-up">Sign Up For Free, Now</p>

								<form onSubmit={this.handleSubmit}>
									<div className="row g-2">
										<div className="col-3">
											<select
												onChange={this.handleChange}
												value={this.state.countryCode}
												name="country-code"
												className={
													"form-select form-field" +
													(this.state.errors.countryCode ? "invalid" : "")
												}
											>
												<option value="+234">+234</option>
											</select>
										</div>
										<div className="col-9 phone-column">
											<div className="form-floating">
												<input
													type="text"
													onChange={this.handlePhone}
													value={this.state.phone}
													name="phone"
													id="phone"
													onBlur={this.validatePhone}
													className={
														"form-control form-field " +
														(this.state.errors.phone ? "error" : "")
													}
													placeholder="Phone Number"
												/>
												<label for="floatingInput">Phone Number</label>
												{this.state.errors.phone && (
													<div className="errorMessage">
														{this.state.errors.phone}
													</div>
												)}
											</div>
										</div>
									</div>
									<div className="col-12 form-group">
										<div className="form-floating">
											<input
												type="text"
												onChange={this.handleFullname}
												onBlur={this.validateFullname}
												value={this.state.fullname}
												name="fullname"
												className={
													"form-control form-field " +
													(this.state.errors.fullname ? "error" : "")
												}
												placeholder="Full Name"
											/>
											<label for="floatingInput">Full Name</label>
											{this.state.errors.fullname && (
													<div className="errorMessage">
														{this.state.errors.fullname}
													</div>
												)}
										</div>
									</div>
									<div className="col-12 form-group">
										<div className="form-floating">
											<input
												type="text"
												onChange={this.handleEmail}
												onBlur={this.validateEmail}
												value={this.state.email}
												name="email"
												className={
													"form-control form-field " +
													(this.state.errors.email ? "error" : "")
												}
												placeholder="E-mail (Optional)"
											/>
											<label for="floatingInput">E-mail</label>
											{this.state.errors.email && (
													<div className="errorMessage">
														{this.state.errors.email}
													</div>
												)}
										</div>
									</div>
									<div className="col-12 form-group">	
										<div className="form-floating">
											<input
												type="password"
												onChange={this.handlePassword}
												onBlur={this.validatePassword}
												value={this.state.password}
												name="password"
												className={
													"form-control form-field " +
													(this.state.errors.password ? "error" : "")
												}
												placeholder="Password"
											/>
											<label for="floatingInput">Password</label>
											{this.state.errors.password && (
													<div className="errorMessage">
														{this.state.errors.password}
													</div>
											)}
										</div>
									</div>
									<div className="col-12 form-group">
										<div className="form-floating">
											<input
												type="password"
												onChange={this.handleRepassword}
												onBlur={this.validateRepassword}
												value={this.state.repassword}
												name="repassword"
												className={
													"form-control form-field " +
													(this.state.errors.password ? "error" : "")
												}
												placeholder="Re-Type Password"
											/>
											<label for="floatingInput">Re-Type PAssword</label>
											{this.state.errors.repassword && (
												<div className="errorMessage">
													{this.state.errors.repassword}
												</div>
												)}
										</div>
									</div>

									<div className="col-12 form-group">
										<p className="password-notes ml-3">
											<span className="text-danger">*</span> Password must be
											at least 7 long and include at least one special
											character.
										</p>
									</div>
									<div className="col-12 form-group">
										<div className="form-group form-check">
											<input
												type="checkbox"
												className={
													"form-check-input " +
													(this.state.errors.check ? "error" : "")
												}
												onChange={this.handleCheck}
												name="check"
												value={this.state.check}
												id="check"
											/>
											<label className="form-check-label">
												I want to be informed of special promotions and news
												regarding jeetar
											</label>
											{this.state.errors.check && (
													<div className="errorMessage">
														{this.state.errors.check}
													</div>
												)}
										</div>
									</div>
									<div className="col-12 form-group">
										<button
											type="submit"
											className="submit form-control btn-primary"
										>
											Create Account
										</button>
									</div>
									<div className="col-12 form-group">
										<img src={continueRule} className="d-block mx-auto" />
									</div>
									<div className="col-12 tw-flex tw-justify-center form-group text-center">
										<a href="#">
											<img src={google} className="mx-3" alt="google" />
										</a>
										<a href="#">
											<img src={facebook} className=" mx-3" alt="facebook" />
										</a>
									</div>
								</form>
							</div>
						</div>
					</div>
				</div>
			</>
		);
	}
}

export default Signup;
