import React from 'react';
import './SelectAddress.css';
import Location from '../img/Location.svg';
import arrowRight from '../img/arrowRight.svg';
const SelectAddress= ()=>{
    return(
       <div className="z-index-1 shadow-sm select__address mt-1 mb-1 d-flex justify-content-between align-items-center py-4 d-block d-sm-none">
			 <div className="location__icon__text__wrapper">
                <span className="location__icon me-2"><img src={Location} /></span> Select Delivery Address
             </div>
             <div>
               <span className="arrow__right me"><img src={arrowRight} /></span>
             </div>
		</div>
    )
}

export default SelectAddress;