
import React from 'react';
import styled from 'styled-components';


const Comingsoon =()=>{
     return(
     		 <>
     		    <ComingSoonSection className="comingsoon"> 
				   <h1>Coming Soon</h1>
    
                </ComingSoonSection>
     		 </>
     	)
}

const ComingSoonSection =styled.div`
    height: 60vh;
    display: flex;
    justify-content:center;
    align-items:center;
    font-family: Gilmer Bold;
	font-style: normal;
	font-weight: bold;
	font-size: 18px;
	line-height: 22px;
	color: #374B58;	
`

export default Comingsoon;