import React from "react";
import phone from "../img/phone.png";
import phone2 from "../img/phone2.svg";
import shopiphone from "../img/shopiphone.svg";
import shopandroid from "../img/shopandroid.svg";
import './Shopping.css'

const Shopping = () => {
	return (
		<>
			<div className="shopping-section">
				<div className="row">
					<div className="col-sm-12 col-md-6 col-lg-6 shopping-left order-2 order-sm-1">
						<div className="icon ps-sm-0 ps-3">
							<h3 className="mt-5  text-sm-left text-center">
								Shopping and delivery made easy, Get all orders in 20 mins
							</h3>
							<p className="my-4 download-text text-sm-left text-center">Download Jeetar On</p>
							<div className="d-flex">
								<a href="" alt="iphone">
									{" "}
									<img src={shopiphone} />
								</a>
								<a href="" alt="android" className="tw-ml-5">
									{" "}
									<img src={shopandroid} />
								</a>
							</div>
						</div>
					</div>
					<div className="col-sm-12 col-md-6 col-lg-6 shopping-right order-1 order-sm-2">
						<img src={phone} alt="fphone" className="d-none d-sm-block" />
						<img src={phone2} alt="phonefull" className="d-block d-sm-none mx-auto mb-0 phonefull" />
					</div>
				</div>
			</div>
		</>
	);
};

export default Shopping;
